"use client";

import React, { useState } from "react";
import { X, PlusCircle, Trash2 } from "lucide-react";
import { useCreateUser } from "@/app/customHooks/useUser";

interface AddUserModalProps {
  onClose: () => void;
}

export default function AddUserModal({ onClose }: AddUserModalProps) {
  const { mutate: createUser, isPending } = useCreateUser(); // âœ… Your existing hook
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    skills: [] as string[],
  });
  const [newSkill, setNewSkill] = useState("");

  const handleAddSkill = () => {
    if (newSkill.trim() && !formData.skills.includes(newSkill.trim())) {
      setFormData({ ...formData, skills: [...formData.skills, newSkill.trim()] });
      setNewSkill("");
    }
  };

  const handleRemoveSkill = (skill: string) => {
    setFormData({
      ...formData,
      skills: formData.skills.filter((s) => s !== skill),
    });
  };

  const handleSave = () => {
    if (!formData.firstName || !formData.lastName || !formData.email) {
      alert("Please fill in all required fields.");
      return;
    }

    createUser(formData, {
      onSuccess: () => {
        onClose(); // âœ… Close modal on success
      },
    });
  };

  return (
    <div className="fixed inset-0 bg-black/40 flex justify-center items-center z-50">
      <div className="bg-white rounded-2xl shadow-2xl p-6 w-[90%] sm:w-[400px] relative">
        <button
          onClick={onClose}
          className="absolute top-3 right-3 text-gray-500 hover:text-gray-700"
        >
          <X size={22} />
        </button>

        <h3 className="text-lg font-semibold mb-4 text-gray-800">Add New User</h3>

        <div className="flex flex-col gap-3">
          <input
            type="text"
            placeholder="First Name"
            value={formData.firstName}
            onChange={(e) => setFormData({ ...formData, firstName: e.target.value })}
            className="border p-2 rounded-md focus:outline-blue-500"
          />
          <input
            type="text"
            placeholder="Last Name"
            value={formData.lastName}
            onChange={(e) => setFormData({ ...formData, lastName: e.target.value })}
            className="border p-2 rounded-md focus:outline-blue-500"
          />
          <input
            type="email"
            placeholder="Email"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            className="border p-2 rounded-md focus:outline-blue-500"
          />
          <input
            type="text"
            placeholder="Phone"
            value={formData.phone}
            onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
            className="border p-2 rounded-md focus:outline-blue-500"
          />

          {/* Skills */}
          <div className="flex items-center gap-2">
            <input
              type="text"
              value={newSkill}
              onChange={(e) => setNewSkill(e.target.value)}
              placeholder="Add Skill"
              className="border p-2 rounded-md flex-1 focus:outline-blue-500"
            />
            <button
              onClick={handleAddSkill}
              className="bg-green-600 text-white px-3 py-2 rounded-md hover:bg-green-700 transition-all"
            >
              <PlusCircle size={18} />
            </button>
          </div>

          <div className="flex flex-wrap gap-2 mt-2">
            {formData.skills.map((skill, i) => (
              <span
                key={`skill-${i}`}
                className="bg-gray-100 border px-2 py-1 rounded-full text-sm flex items-center gap-1"
              >
                {skill}
                <button
                  onClick={() => handleRemoveSkill(skill)}
                  className="text-red-500 hover:text-red-700"
                >
                  <Trash2 size={14} />
                </button>
              </span>
            ))}
          </div>
        </div>

        <div className="flex justify-end gap-3 mt-6">
          <button
            onClick={onClose}
            className="px-4 py-2 border rounded-md hover:bg-gray-100 transition-all"
          >
            Cancel
          </button>
          <button
            onClick={handleSave}
            disabled={isPending}
            className={`px-4 py-2 rounded-md text-white transition-all ${
              isPending ? "bg-gray-400" : "bg-blue-600 hover:bg-blue-700"
            }`}
          >
            {isPending ? "Saving..." : "Save"}
          </button>
        </div>
      </div>
    </div>
  );
}
